.Comment-course {
  width: 100%;
  display: block;
  background: #f6f7fb;
}
.Comment-course .school-warp {
  width: 1100px;
  margin: 0 auto;
}
.Comment-course .school-warpa {
  width: 1167px;
  margin: 0 auto;
}
.Comment-course .arrows-rank {
  float: left;
  margin: 7px 0 0 5px;
  width: 10px;
}
.Comment-course .arrows-rank span {
  margin: 3px 0;
  width: 0;
  height: 0;
  display: block;
  border-radius: 5px;
  cursor: pointer;
}
.Comment-course .arrows-rank .arrows-rank-up {
  border-bottom: 7px solid #C6C6C6;
  border-right: 8px solid transparent;
  border-left: 8px solid transparent;
}
.Comment-course .arrows-rank .arrows-rank-down {
  border-top: 7px solid #C6C6C6;
  border-right: 8px solid transparent;
  border-left: 8px solid transparent;
}
.Comment-course .rank-text {
  float: left;
  font-style: normal;
  font-size: 20px;
  line-height: 38px;
  color: #0D1731;
  opacity: 1;
  margin-bottom: 0;
  cursor: pointer;
}
.Comment-course .Comment-radiuo .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  background: #68A4FF !important;
}
.Comment-course .pagebanner {
  padding-top: 76px;
  display: block;
  width: 100%;
  height: 216px;
  min-width: 1100px;
}
.Comment-course .pagebanner .page-mian {
  position: relative;
  display: block;
  text-align: center;
}
.Comment-course .pagebanner .page-mian .text {
  background: linear-gradient(270deg, #396FB4 0%, rgba(57, 111, 180, 0) 100%);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Comment-course .pagebanner .page-mian .text .h4 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 120.7%;
  color: #FFFFFF;
  display: inline-block;
  margin-right: 10px;
  margin-bottom: 0;
}
.Comment-course .pagebanner .page-mian .text .span {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #FFFFFF;
  opacity: 0.7;
  display: inline-block;
}
.Comment-course .pagebanner .page-mian .text .banner-search {
  float: right;
  width: 428px;
  height: 38px;
  background: #FFFFFF;
  border-radius: 6px;
  margin-right: 30px;
}
.Comment-course .pagebanner .page-mian .text .banner-search .ant-input {
  width: 100%;
  display: block;
}
.Comment-course .pagebanner .page-mian .text .banner-search .ant-input-search-icon {
  color: #1A8ECC;
}
.Comment-course .course-search {
  width: 100%;
  display: block;
  background: #f6f7fb;
}
.Comment-course .course-search .ant-card-body {
  padding: 0;
}
.Comment-course .course-search .paper-common-course {
  padding: 12px 20px;
}
.Comment-course .course-search .paper-common-course .paper-year {
  color: #2A283D;
  font-weight: bold;
  display: inline-block;
  text-align: center;
  height: 38px;
  line-height: 38px;
}
.Comment-course .course-search .paper-common-course .paper-common-btn .ant-btn > .anticon + span,
.Comment-course .course-search .paper-common-course .paper-common-btn .ant-btn > span + .anticon {
  color: #C6C6C6;
  margin-top: -2px;
}
.Comment-course .course-search .paper-common-course .paper-common-btn .ant-btn {
  border: none;
  background: none;
  box-shadow: none!important;
}
.Comment-course .course-search .paper-common-course .paper-common-btn .ant-btn.ant-btn-primary {
  height: 38px;
  line-height: 38px;
  margin-bottom: 0;
  background: none!important;
  border-radius: 2px;
}
.Comment-course .course-search .paper-common-course .paper-common-btn .ant-btn.ant-btn-primary span {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 38px;
  color: #0D1731;
  opacity: 1;
}
.Comment-course .course-search .paper-common-course .paper-common-btn .ant-btn span {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 120.7%;
  color: #0D1731;
  opacity: 0.6;
}
.Comment-course .course-search .paper-common-course .paper-common-search {
  text-align: right;
  width: 100%;
}
.Comment-course .course-search .paper-common-course .paper-common-search .ant-input-affix-wrapper .ant-input {
  background: #F6F7FB;
  border-radius: 2px;
  height: 38px;
}
.Comment-course .course-search .paper-common-course .paper-common-search .paper-common-totale {
  margin-left: 20px;
  color: #000000;
  opacity: 0.3;
}
.Comment-course .course-search .paper-common-course .paper-common-search .ant-input-search-icon {
  color: #68A4FF;
}
.Comment-course .recommend-main {
  width: 100%;
  display: block;
  padding-top: 30px;
}
.Comment-course .recommend-main .recommend-li {
  width: 29.666%;
  margin-right: 5.5%;
  background: #FFFFFF;
  border-radius: 12px;
  overflow: hidden;
}
.Comment-course .recommend-main .recommend-li .li {
  padding: 27px 20px;
  display: block;
  width: 100%;
  position: relative;
}
.Comment-course .recommend-main .recommend-li .li .img {
  width: 100%;
  height: 178px;
}
.Comment-course .recommend-main .recommend-li .li .img img {
  width: 100%;
  height: 100%;
  display: block;
  border-radius: 5px;
}
.Comment-course .recommend-main .recommend-li .li .new {
  width: 120px;
  height: 120px;
  position: absolute;
  background: #FA5454;
  top: -60px;
  left: -60px;
  transform: rotate(45deg);
}
.Comment-course .recommend-main .recommend-li .li .new i {
  position: absolute;
  bottom: 50px;
  right: -12px;
  font-style: normal;
  font-weight: bold;
  font-size: 14.07px;
  line-height: 19px;
  color: #FFFFFF;
  transform: rotate(-90deg);
}
.Comment-course .recommend-main .recommend-li .li .title {
  margin-top: 20px;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 33px;
  color: #0D1731;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  margin-bottom: 10px;
}
.Comment-course .recommend-main .recommend-li .li .text {
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 19px;
  color: #0D1731;
  opacity: 0.3;
  height: 76px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
}
.Comment-course .recommend-main .recommend-li .li .text-a {
  margin-top: 10px;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 19px;
  color: #0D1731;
  opacity: 0.5;
  display: block;
  margin-bottom: 8px;
}
.Comment-course .recommend-main .recommend-li .li .text-a span {
  display: inline-block;
  margin-right: 10px;
}
.Comment-course .recommend-main .recommend-li .li .time {
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 19px;
  color: #0D1731;
  opacity: 0.5;
  display: block;
}
.Comment-course .recommend-main .recommend-li .li .time span {
  width: 50%;
  float: left;
}
.Comment-course .recommend-main .recommend-li .li .time span:nth-child(2n) {
  float: right;
  text-align: right;
}
.Comment-course .recommend-main .recommend-li .li .time span:nth-child(2n) img {
  width: 10px;
  height: 12px;
  margin-top: -4px;
  margin-right: 5px;
}
.Comment-course .recommend-main .recommend-li:hover .title {
  color: #68A4FF;
}
.Comment-course .recommend-main .recommend-li:nth-child(3n) {
  margin-right: 0;
}
.Comment-course .Online-main {
  display: block;
}
.Comment-course .Online-main .Online {
  margin-top: 20px;
  width: 100%;
  display: block;
  padding: 10px 0 25px;
}
.Comment-course .Online-main .Online .Online-bottom {
  padding: 30px 0 0;
  display: block;
  width: 100%;
  min-height: 200px;
}
.Comment-course .Online-main .Online .Online-bottom .ant-col-6 {
  width: 21.25%;
  margin-right: 5%;
  margin-bottom: 40px;
}
.Comment-course .Online-main .Online .Online-bottom .ant-col-6:nth-child(4n) {
  margin-right: 0;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic {
  width: 100%;
  display: block;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic .img {
  width: 100%;
  height: 141px;
  display: block;
  margin-bottom: 15px;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic .img img {
  width: 100%;
  height: 100%;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic .title {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 25px;
  color: #0D1731;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  margin-bottom: 5px;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic .text {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: #0D1731;
  opacity: 0.5;
  display: block;
  margin-bottom: 5px;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic .text span {
  display: inline-block;
  margin-right: 10px;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic .time {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: #0D1731;
  opacity: 0.5;
  display: block;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic .time span {
  width: 50%;
  float: left;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic .time span:nth-child(2n) {
  float: right;
  text-align: right;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic .time span:nth-child(2n) img {
  width: 10px;
  height: 12px;
  margin-top: -4px;
  margin-right: 5px;
}
.Comment-course .Online-main .Online .Online-bottom .li-pic:hover .title {
  color: #0068C3;
}
.Comment-course .more-link {
  overflow: hidden;
  width: 100%;
  text-align: center;
  font-size: 20px;
  line-height: 40px;
  color: #4481BA;
  opacity: 0.5;
  cursor: pointer;
}
